// TODO: move into sagas
import store from 'store2';

const ALLOWED_REDIRECTS = ['me.mssg.next://auth/cb', /^https?:\/\/mssg\.test/, /^https?:\/\/localhost/]; // temporary solution

export function isAllowedRedirect(redirectTo) {
    return ALLOWED_REDIRECTS.some((url) =>
        typeof url === 'string' ? url === redirectTo : url.test(redirectTo)
    );
}

export function generateRedirectUrl() {
    const locationUrl = new URL(window.location.href);
    const redirectTo = locationUrl.searchParams.get('redirect_uri');

    if (isAllowedRedirect(redirectTo)) {
        return `${redirectTo}?access_token=${store.get('accessToken')}`;
    }
    console.warn('Redirect to unknown url', redirectTo);
    return null;
}

export function redirectAfterLogin() {
    window.location.replace(generateRedirectUrl() || '/pages');
}

export function redirectAfterRegistration() {
    window.location.replace(generateRedirectUrl() || '/pages/create');
}
